import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';

import SocketProvider from './components/SocketProvider'
import universal from 'react-universal-component';

let Landing = universal(() => import('./routes/Landing/Landing'))
let Directory = universal(() => import('./routes/Directory/Directory'))
let Workspace = universal(() => import('./routes/Workspace/Workspace'))
let Unknown = universal(() => import('./routes/Unknown/Unknown'))

/**
 * The `App` component is the entry point for the react app.
 * It is rendered on the client as well as on the server.
 *
 * You can start developing your react app here.
 */
class App extends Component {
  render = () =>
    <SocketProvider>
      <Switch>
        <Route exact path='/' component={Landing}/>
        <Route path='/q' component={Directory}/>
        <Route path='/a/:id' component={Workspace}/>
        <Route otherwise component={Unknown}/>
      </Switch>
    </SocketProvider>
}

export default App
